.content-body {
    &.noteDetail-content {
        padding-top: 51px;
        background: #fcfcfc;
        min-height: 100%;
        .noteDetail-container {
            .noteDetail-subject {
                padding-left: 18px;
                color: #fff;
                h2 {
                    @include fontSize(25px,700);
                    letter-spacing: -.7px;
                }
            }
            .swiper-container.noteSlide {
                overflow: inherit;
                padding: 0 21px;
                .swiper-wrapper {
                    height: calc( 100vh - 110px );
                    .swiper-slide {
                        position: relative;
                        background: #fff;
                        border-radius: 11px;
                        .noteDetail-inner {
                            width: 100%;
                            height: 100%;
                            box-shadow: 7px 15px 24px 0 rgba(0,0,0,.03);
                            .word-box {
                                height: 97px;
                                text-align: center;
                                .korean {
                                    @include fontSize(26px,700);
                                    color: #4d4d4d;
                                    line-height: 39px;
                                    padding-top: 14px;
                                }
                                .vietnam {
                                    padding-top: 2px;
                                    @include fontSize(18px,400);
                                    line-height: 21px;
                                    color: #878787;
                                }
                            }
                            .img-box {
                                background: url(../img/camera.png) center center;
                                background-size: cover;
                                height: calc( 100% - 275px );
                            }
                            .item-box {
                                position: relative;
                                text-align: center;
                                padding-top: 25px;
                                .sound-item {
                                    position: absolute;
                                    right: 14px;
                                    top: -33px;
                                    background: none;
                                    border: 0;
                                }
                                span {
                                    display: block;
                                    @include fontSize(12px,700);
                                    color: #0ad09a;
                                    margin-bottom: 5px;
                                }
                                .steping {
                                    height: 60px;
                                    @include flexAllCenter;
                                    >div {
                                        display: none;
                                        &.active {
                                            display: block;
                                        }
                                    }
                                    .default-item {
                                        .shape {
                                            @include fontSize(20px,700);
                                            color: #565656;
                                            &.active {
                                                display: none;
                                            }
                                        }
                                    }
                                    .first-item,,.second-item,.last-item {
                                        display: none;
                                        @keyframes loading {
                                            0% {
                                                width: 2px;
                                                height: 2px;
                                            }
                                            50% {
                                                width: 8px;
                                                height: 8px;
                                            }
                                            100% {
                                                width: 2px;
                                                height: 2px;
                                            }
                                        }
                                        &.active {
                                            display: block;
                                        }
                                        .loading {
                                            width: 53px;
                                            margin: 0 auto 8px;
                                            &:after {
                                                @include clearBoth;
                                            }
                                            > div {
                                                width: 8px;
                                                height: 8px;
                                                float: left;
                                                margin-right: 7px;
                                                @include flexAllCenter;
                                                &:nth-child(1) {
                                                    div {
                                                        background: #D1E7BC;
                                                        animation: loading .8s .1s infinite;
                                                    }
                                                }
                                                &:nth-child(2) {
                                                    div {
                                                        background: #80E096;
                                                        animation: loading .8s .2s infinite;
                                                    }
                                                }
                                                &:nth-child(3) {
                                                    div {
                                                        background: #81BD9F;
                                                        animation: loading .8s .3s infinite;
                                                    }
                                                }
                                                &:last-of-type {
                                                    margin-right: 0;
                                                    div {
                                                        background: #4BBBC0;
                                                        animation: loading .8s .4s infinite;
                                                    }
                                                }
                                                > div {
                                                    width: 2px;
                                                    height: 2px;
                                                    border-radius: 9px;
                                                }
                                            }
                                        }
                                        p {
                                            color: #959595;
                                            @include fontSize(12px,500);
                                        }
                                    }
                                    .last-item {
                                        p {
                                            @include fontSize(12px,500);
                                            color: #959595;
                                            img {
                                                margin-left: 3px;
                                            }
                                        }
                                        strong {
                                            color: #0ad09a;
                                            @include fontSize(20px,700);
                                            margin-bottom: 5px;
                                            display: block;
                                            &.bad {
                                                color: #f35858;
                                            }
                                        }
                                    }
                                }
                                .btn-step {
                                    width: 198px;
                                    margin: 0 auto;
                                    div {
                                        width: 100%;
                                        display: none;
                                        height: 42px;
                                        border-radius: 21px;
                                        border: 0;
                                        color: #fff;
                                        @include fontSize(12px,500);
                                        img {
                                            margin-right: 6px;
                                        }
                                        &.active {
                                            @include flexAllCenter
                                        }
                                        &.default-btn,&.first-step,&.last-step {
                                            background: #0ad09a;
                                        }
                                        &.second-step {
                                            background: #d4d6da;
                                        }
                                    }
                                }
                                .my-action {
                                    display: none;
                                    padding-top: 7px;
                                    &.active {
                                        display: block;
                                    }
                                    >button {
                                        background: none;
                                        border: 0;
                                        @include fontSize(10px,500);
                                        color: #959595;
                                    }
                                }
                            }
                        }
                    }
                }
                .swiper-pagination {
                    bottom: -32px;
                    &.swiper-pagination-bullets {
                        .swiper-pagination-bullet {
                            margin-left: 0;
                            margin-right: 5px;
                            width: 4px;
                            height: 4px;
                            background: #d8d8d8;
                            border-radius: 2px;
                            opacity: 1;
                        }
                        .swiper-pagination-bullet-active {
                            width: 14px;
                            background: #0ad09a;
                            transition: .3s;
                        }
                    }
                }
            }
        }
    }
}