.content-body {
    &.search-content {
        .search-wrapper {
            position: relative;
            .search-bar {
                width: 100%;
                height: 64px;
                border: .7px solid #ededed;
                background: #f9f9f9;
                padding: 0 42px 0 19px;
                @include fontSize(23px,500);
                color: #383838;
                letter-spacing: -.6px;
            }
            .search-delete {
                display: none;
                background: none;
                border: 0;
                position: absolute;
                right: 19px;
                top: 50%;
                transform: translate(0,-50%);
                &.active {
                    display: block;
                }
            }
        }
        .searchList-wrapper {
            margin-top: 31px;
            &.hideing {
                display: none;
            }
            .searchList-title {
                p {
                    @include fontSize(16px,500);
                    letter-spacing: -.4px;
                    color: #565656;
                }
            }
            ul {
                li {
                    border-bottom: .7px solid #e6e6e6;
                    padding: 17px 34px 17px 6px;
                    position: relative;
                    &:after {
                        @include clearBoth;
                    }
                    .subject {
                        float: left;
                        span {
                            @include fontSize(13px,400);
                            color: #616161;
                        }
                    }
                    .date-box {
                        float: right;
                        span {
                            @include fontSize(13px,400);
                            color: #afafaf;
                            letter-spacing: -.3px;
                        }
                    }
                    button {
                        position: absolute;
                        top: 50%;
                        right: 7.5px;
                        transform: translate(0,-50%);
                        background: none;
                        border: 0;
                    }
                }
            }
        }
        .videoList-wrapper {
            margin-top: 15px;
            display: none;
            &.showing {
                display: block;
            }
            &.none-list {
                .none-list {
                    display: block;
                    text-align: center;
                    padding-top: 16%;
                    h2 {
                        margin: 10px 0;
                        @include fontSize(21px,700);
                        color: #46484f;
                        line-height: 27px;
                    }
                }
                .videoList-inner {
                    display: none;
                }
            }
            .none-list {
                display: none;
            }
            .videoList-inner {
                border-radius: 11px;
                overflow: hidden;
                box-shadow: 7px 18px 24px 0 rgba(0,0,0,.07);
                margin-bottom: 14px;
                &:last-of-type {
                    margin-bottom: 0;
                }
                .thumbnail-box {
                    background: url(../img/follow.png) center center;
                    background-size: cover;
                    height: 185px;
                    position: relative;
                    overflow: hidden;
                    .point-num {
                        z-index: 1;
                        position: absolute;
                        left: 15px;
                        bottom: 9px;
                        color: #868686;
                        @include fontSize(11px,700);
                        span {
                            color: #0ad09a;
                            margin-right: 2px;
                        }
                    }
                    .video-timer {
                        z-index: 1;
                        position: absolute;
                        right: 8px;
                        bottom: 9px;
                        background: rgba(0,0,0,.5);
                        width: 35px;
                        height: 17px;
                        border-radius: 99px;
                        @include flexAllCenter;
                        span {
                            @include fontSize(10px,400);
                            color: rgba(255,255,255,.8);
                        }
                    }
                    .shadow-box {
                        position: absolute;
                        bottom: -25px;
                        width: 100%;
                        height: 116px;
                        opacity: .78;
                        background: linear-gradient(to bottom, rgba(0,0,0,0),#000);
                    }
                }
                .videoInfo-box {
                    padding: 19px 19px 15px;
                    .korean {
                        &:after {
                            @include clearBoth;
                        }
                        h2 {
                            float: left;
                            @include fontSize(15px,700);
                            letter-spacing: -.5px;
                            color: #000;
                            img {
                                width: 20px;
                                margin-left: 5px;
                                margin-bottom: 3px;
                            }
                        }
                    }
                    .vietnam {
                        margin-top: 7px;
                        p {
                            color: #a2a2a2;
                            @include fontSize(14px,400);
                        }
                    }
                    .video-title {
                        margin-top: 18px;
                        @include flexCenter;
                        .video-profile {
                            width: 20px;
                            height: 20px;
                            border-radius: 10px;
                            background: url(../img/song.png) center center;
                            background-size: cover;
                            margin-right: 4px;
                        }
                        p {
                            color: #adadad;
                            letter-spacing: -.5px;
                            @include fontSize(14px,400);
                        }
                    }
                }
            }
        }
    }
}