.content-body {
    &.play-content {
        .play-container {
            position : relative;
            width : 100%;
            height : 0;
            padding-bottom : 56.25%;
            #ytplayer {
                position : absolute;
                top : 0;
                left : 0;
                width : 100%;
                height : 100%;
            }
            .bar-timer {
                position: absolute;
                width: 100%;
                bottom: 0;
                .video-timer {
                    z-index: 1;
                    position: absolute;
                    right: 8px;
                    bottom: 12.5px;
                    background: rgba(0,0,0,.5);
                    width: 51px;
                    height: 20px;
                    border-radius: 99px;
                    @include flexAllCenter;
                    span {
                        @include fontSize(12px,400);
                        color: rgba(255,255,255,.8);
                    }
                }
                .watching-bar {
                    height: 3px;
                    width: 100%;
                    background: #c9c9cb;
                    .color-bar {
                        height: 3px;
                        width: 20%;
                        background: #0ad09a;
                    }
                }
            }
        }
        .word-container {
            .swiper-container.quizSlide {
                .swiper-wrapper {
                    .swiper-slide {
                        .word-wrapper {
                            position: relative;
                            padding: 19px 21px 0;
                            .language-inner {
                                >div {
                                    margin-bottom: 13px;
                                    &:last-of-type {
                                        margin-bottom: 0;
                                    }
                                    &.korean {
                                        &:after {
                                            @include clearBoth;
                                        }
                                        p {
                                            float: left;
                                            line-height: 18px;
                                            @include fontSize(19px,700);
                                            color: #292e42;
                                            letter-spacing: -.7px;
                                        }
                                        img {
                                            width: 20px;
                                            float: left;
                                            margin-left: 6px;
                                        }
                                    }
                                    &.vietnam {
                                        p {
                                            @include fontSize(14px,400);
                                            color: #80838f;
                                            letter-spacing: -.3px;
                                        }
                                    }
                                    &.english {
                                        p {
                                            @include fontSize(14px,400);
                                            color: rgba(#999ca6,.85);
                                            letter-spacing: -.3px;
                                        }
                                    }
                                }
                            }
                            .bottom-wrapper {
                                position: absolute;
                                bottom: 54px;
                                ul {
                                    li {
                                        @include fontSize(12px,500);
                                        color: rgba(#292e42,.6);
                                        letter-spacing: -.4px;
                                        margin-bottom: 3px;
                                        &:last-of-type {
                                            margin-bottom: 0;
                                        }
                                        span {
                                            margin-left: 2px;
                                            &:first-child {
                                                margin-right: 2px;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                .swiper-pagination {
                    height: 26px;
                    @include flexAllCenter;
                    color: #d0d2dc;
                    &.en-custom {
                        bottom: 232px;
                    }
                    >span {
                        margin-left: 5px;
                        font-family: 'SpoqaHanSans','Sans-serif';
                        @include fontSize(14px,700);
                        padding-bottom: 3px;
                        &.swiper-pagination-current {
                            color: #4f4f4f;
                            margin-right: 5px;
                            margin-left: 0;
                        }
                    }
                    &:after {
                        content: "1x";
                        display: block;
                        position: absolute;
                        right: 18px;
                        top: 0;
                        border: 1px solid #000;
                        @include fontSize(12px,900);
                        color: #6c6c6c;
                        width: 55px;
                        height: 26px;
                        border: 1px solid rgba(#929292,.4);
                        border-radius: 14px;
                        @include flexAllCenter;
                    }
                    &:before {
                        font-family: 'Noto Sans KR';
                        content: "한/베";
                        display: block;
                        position: absolute;
                        left: 18px;
                        top: 0;
                        border: 1px solid #000;
                        @include fontSize(12px,900);
                        color: #6c6c6c;
                        width: 55px;
                        height: 26px;
                        border: 1px solid rgba(#929292,.4);
                        border-radius: 14px;
                        @include flexAllCenter;
                    }
                }
                .swiper-button-next,.swiper-button-prev {
                    top: inherit;
                    bottom: 240px;
                    width: 7px;
                    margin-top: 0;
                    height: 12px;
                    &.swiper-button-disabled {
                        opacity: 1;
                    }
                }
                .swiper-button-next {
                    right: calc( 50% - 39px );
                    background: url(../img/ic-next-mini.png);
                    background-size: cover;
                }
                .swiper-button-prev {
                    left: calc( 50% - 39px );
                    background: url(../img/ic-prev-mini.png);
                    background-size: cover;
                }
            }
        }
        .quiz-container {
            background: rgba(#efefef,.2);
            height: 213px;
            position: relative;
            .start-area {
                z-index: 2;
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
                background: rgba(#efefef,.2);
                @include flexAllCenter;
                &.hide {
                    display: none;
                }
                .start-inner {
                    text-align: center;
                    h2 {
                        color: #292e42;
                        @include fontSize(19px,700);
                        letter-spacing: -.7px;
                        margin-bottom: 13px;
                    }
                    p {
                        color: rgba(#1d1d1d,.6);
                        @include fontSize(12px,400);
                        @include flexAllCenter;
                        img {
                            margin-right: 6px;
                        }
                    }
                }
            }
            .quizItem-wrapper {
                .swiper-container.quizItem {
                    height: 213px;
                    padding: 17px 28px;
                    .swiper-wrapper {
                        .swiper-slide {
                            width: 100%;
                            background: #fff;
                            border-radius: 11px;
                            padding: 11px 20px 28px;
                            box-shadow: 7px 15px 24px 0 rgba(0,0,0,.12);
                            .steping {
                                height: 76px;
                                @include flexAllCenter;
                                >div {
                                    display: none;
                                    &.active {
                                        display: block;
                                    }
                                }
                                .default-item {
                                    .shape {
                                        @include fontSize(20px,700);
                                        color: #565656;
                                        &.active {
                                            display: none;
                                        }
                                    }
                                }
                                .first-item,,.second-item,.last-item {
                                    display: none;
                                    @keyframes loading {
                                        0% {
                                            width: 2px;
                                            height: 2px;
                                        }
                                        50% {
                                            width: 8px;
                                            height: 8px;
                                        }
                                        100% {
                                            width: 2px;
                                            height: 2px;
                                        }
                                    }
                                    &.active {
                                        display: block;
                                    }
                                    .loading {
                                        width: 53px;
                                        margin: 0 auto 8px;
                                        &:after {
                                            @include clearBoth;
                                        }
                                        > div {
                                            width: 8px;
                                            height: 8px;
                                            float: left;
                                            margin-right: 7px;
                                            @include flexAllCenter;
                                            &:nth-child(1) {
                                                div {
                                                    background: #D1E7BC;
                                                    animation: loading .8s .1s infinite;
                                                }
                                            }
                                            &:nth-child(2) {
                                                div {
                                                    background: #80E096;
                                                    animation: loading .8s .2s infinite;
                                                }
                                            }
                                            &:nth-child(3) {
                                                div {
                                                    background: #81BD9F;
                                                    animation: loading .8s .3s infinite;
                                                }
                                            }
                                            &:last-of-type {
                                                margin-right: 0;
                                                div {
                                                    background: #4BBBC0;
                                                    animation: loading .8s .4s infinite;
                                                }
                                            }
                                            > div {
                                                width: 2px;
                                                height: 2px;
                                                border-radius: 9px;
                                            }
                                        }
                                    }
                                    p {
                                        color: #959595;
                                        @include fontSize(12px,500);
                                    }
                                }
                                .last-item {
                                    p {
                                        @include fontSize(12px,500);
                                        color: #959595;
                                        img {
                                            margin-left: 3px;
                                        }
                                    }
                                    strong {
                                        color: #0ad09a;
                                        @include fontSize(20px,700);
                                        margin-bottom: 5px;
                                        display: block;
                                        &.bad {
                                            color: #f35858;
                                        }
                                    }
                                }
                            }
                            .btn-step {
                                width: 198px;
                                margin: 0 auto;
                                div {
                                    width: 100%;
                                    display: none;
                                    height: 42px;
                                    border-radius: 21px;
                                    border: 0;
                                    color: #fff;
                                    @include fontSize(12px,500);
                                    img {
                                        margin-right: 6px;
                                    }
                                    &.active {
                                        @include flexAllCenter
                                    }
                                    &.default-btn,&.first-step,&.last-step {
                                        background: #0ad09a;
                                    }
                                    &.second-step {
                                        background: #d4d6da;
                                    }
                                }
                            }
                            .my-action {
                                display: none;
                                padding-top: 7px;
                                &.active {
                                    @include flexAllCenter;
                                }
                                >button {
                                    background: none;
                                    border: 0;
                                    @include fontSize(10px,500);
                                    color: #959595;
                                }
                            }
                            .title {
                                @include fontSize(13px,700);
                                color: rgba(#3f4661,.8);
                                line-height: 18px;
                                span {
                                    line-height: 18px;
                                    margin-right: 5px;
                                    color: #0ad09a;
                                    @include fontSize(10px,700);
                                }
                            }
                            .click-quiz {
                                margin-top: 10px;
                                .click-target {
                                    width: 100%;
                                    height: 38px;
                                    @include flexAllCenter;
                                    border: .7px solid rgba(#cfcfcf,.6);
                                    .flexing {
                                        span {
                                            @include fontSize(12px,700);
                                            color: #3f4661;
                                            margin-right: 3px;
                                        }
                                        .placeholder {
                                            @include fontSize(13px,300);
                                            color: rgba(#80838f,.4);
                                        }
                                    }
                                }
                                .click-item {
                                    margin: 3px auto 0;
                                    .item-block {
                                        .item-inner {
                                            @include flexAllCenter;
                                            span {
                                                padding: 6px 12px;
                                                background: #f6f6f6;
                                                color: #aeaeae;
                                                @include fontSize(12px,400);
                                                border-radius: 99px;
                                                margin-top: 9px;
                                                display: block;
                                                float: left;
                                                margin-right: 5px;
                                                &.clicking {
                                                    background: #0ad09a;
                                                    color: #fff;
                                                }
                                                &:last-of-type {
                                                    margin-right: 0;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}