.content-body {
    &.videoProfile-content {
        .thumbnail-container {
            background: url(../img/human.png) center center;
            background-size: cover;
            height: 321px;
            position: fixed;
            top: 0;
            width: 100%;
            .top-shadow {
                top: 0;
                left: 0;
                right: 0;
                position: absolute;
                height: 66px;
                background: linear-gradient(to bottom, #000000, rgba(0, 0, 0, 0));
                opacity: .3;
            }
            .bottom-shadow {
                height: 229px;
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                background:  linear-gradient(to bottom, rgba(0, 0, 0, 0), #000000 96%);
                opacity: .3;
            }
        }
        .videoInfo-container {
            height: calc( 100vh - 298px );
            position: fixed;
            bottom: 0;
            width: 100%;
            background: #fbfbfb;
            border-radius: 21px 21px 0 0;
            padding: 19px 19px 0;
            &.back {
                animation: touchE .7s ease-in-out both;
                @keyframes touchE {
                    0% {
                        height: calc( 100vh - 66px );
                        border-radius: 0;
                        
                    }
                    100% {
                        height: calc( 100vh - 303px );
                        border-radius: 21px 21px 0 0;
                    }
                }
            }
            &.touchstart {
                height: calc( 100vh - 303px );
                translate: .3s;
            }
            &.touchend {
                animation: touchS .7s ease-in-out both;
                overflow-y: scroll;
                @keyframes touchS {
                    0% {
                        height: calc( 100vh - 303px );
                    }
                    100% {
                        height: calc( 100vh - 66px );
                        border-radius: 0;
                    }
                }
            }
            .text-wrapper {
                .english {
                    @include fontSize(25px,700);
                    color: #232631;
                    line-height: 29px;
                }
                .korean {
                    margin-top: 4px;
                    @include fontSize(17px,700);
                    color: #9396a1;
                    letter-spacing: -.7px;
                    line-height: 26px;
                }
                .vietnam {
                    margin-top: 9px;
                    line-height: 25px;
                    color: rgba(#1d1d1d,.4);
                    @include fontSize(14px,400);
                    &.shrink {
                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 3;
                        overflow: hidden;
                    }
                }
                .more-btn {
                    margin-top: 5px;
                    background: none;
                    border: 0;
                    @include flexAllCenter;
                    color: rgba(#1d1d1d,.5);
                    @include fontSize(14px,700);
                }
            }
            .video-wrapper {
                margin-top: 30px;
                .title-inner {
                    margin-bottom: 11px;
                    h2 {
                        @include fontSize(17px,700);
                        color: #232631;
                        letter-spacing: -.4px;
                        line-height: 26px;
                    }
                }
                .list-inner {
                    .list-item {
                        margin-bottom: 15px;
                        &:after {
                            @include clearBoth;
                        }
                        .list-thumbnail {
                            background: url(../img/ssam.png) center center;
                            background-size: cover;
                            width: 160px;
                            height: 89px;
                            float: left;
                            position: relative;
                            .bar-timer {
                                position: absolute;
                                width: 100%;
                                bottom: 0;
                                .video-timer {
                                    z-index: 1;
                                    position: absolute;
                                    right: 4px;
                                    bottom: 7px;
                                    background: rgba(0,0,0,.5);
                                    width: 36px;
                                    height: 16px;
                                    border-radius: 99px;
                                    @include flexAllCenter;
                                    span {
                                        @include fontSize(10px,500);
                                        color: rgba(255,255,255,.8);
                                    }
                                }
                                .watching-bar {
                                    height: 3px;
                                    width: 100%;
                                    background: #c9c9cb;
                                    .color-bar {
                                        height: 3px;
                                        width: 20%;
                                        background: #0ad09a;
                                    }
                                }
                            }
                        }
                        .list-info {
                            width: calc( 100% - 160px );
                            float: right;
                            padding: 0 8px;
                            .video-subject {
                                @include fontSize(13px,400);
                                color: #373737;
                                word-break: break-all;
                                line-height: 17px;
                            }
                            .video-sources {
                                margin-top: 3px;
                                @include fontSize(13px,400);
                                color: #ababab;
                                letter-spacing: -.6px;
                            }
                        }
                    }
                }
            }
        }
    }
}